import React, {Component} from 'react';
import Slider from "react-slick";
import burgerone from '../images/burgerone.jpg';
import burgertwo from '../images/burgertwo.jpg';
import burgerthree from '../images/burgerthree.jpg';
import burgerfour from '../images/burgerfour.jpg';
import burgerfive from '../images/burgerfive.jpg';
import burgersix from '../images/burgersix.jpg';
import berry from '../images/berry.jpg';
import 'slick-carousel/slick/slick-theme.css';
import 'slick-carousel/slick/slick.css';
import '../App.css';

import model from '../model/PageModel';

class BackgroundSlider extends Component {

  constructor(props){
    super(props);
    this.state = {
       images: []
    }
  }

  componentWillMount(){
    this.setState({images: model.images});
    console.log({berry});
  }

  render(){

    const settings = {
     dots: false,
     infinite: true,
     speed: 200,
     fade: true,
     arrow: false,
     lazyload: true,
     slidesToShow: 1,
     slidesToScroll: 1,
     autoplay: true,
     adaptiveHeight: true
   };

    const images = this.state.images.map((image, key) =>(
        <div className="mySlides" key= {key}>
            <img src={image} alt= 'image slide' style={{width: '100%'}}/>
        </div>
    ));
    return(
        <Slider {...settings}>
          <div className="mySlides">
              <img src={burgerone} alt= 'image slide' style={{width: '100%'}}/>
          </div>

          <div className="mySlides">
              <img src={burgertwo} alt= 'image slide' style={{width: '100%'}}/>
          </div>
          <div className="mySlides">
              <img src={burgerthree} alt= 'image slide' style={{width: '100%'}}/>
          </div>
          <div className="mySlides">
              <img src={burgerfour} alt= 'image slide' style={{width: '100%'}}/>
          </div>
          <div className="mySlides">
              <img src={burgerfive} alt= 'image slide' style={{width: '100%'}}/>
          </div>
          <div className="mySlides">
              <img src={burgersix} alt= 'image slide' style={{width: '100%'}}/>
          </div>
        </Slider>
    );
  }
}


export default BackgroundSlider;
